@import "../../scss/var.scss";
.form {
  *::placeholder {
    color: $text;
    opacity: 0.8;
  }
  width: 500px;
  max-width: 95%;
  margin: 0 auto;
  padding: 30px 20px;
  border-radius: 5px;
  background-color: $primary;
  color: $text;
  font-size: 1.5rem;
  text-align: center;

  .item {
    margin-bottom: 10px;
    border-bottom: 2px solid $text;
    padding-bottom: 10px;

    .input {
      width: 100%;
      border: none;
      background: transparent;
      height: 30px;
      color: $text;
    }

    .error {
      border: 2px solid $accent;
    }
    .errorMsg {
      color: $accent;
      font-size: 1rem;
    }
  }
}
